/* src/components/AnimalDetail.module.css */
.detailContainer {
  background-color: var(--color-card-bg);
  padding: 1rem; /* Reduzido para mobile, aumentado em media query */
  border-radius: var(--border-radius);
  box-shadow: 0 4px 12px var(--color-shadow);
  margin-top: 1rem;
}

.backButton {
  background-color: var(--color-secondary);
  color: white;
  border: none;
  padding: 0.6rem 1.2rem; /* Ajustado */
  border-radius: var(--border-radius);
  font-family: var(--font-primary);
  font-size: 0.9rem; /* Ajustado */
  cursor: pointer;
  transition: background-color 0.2s;
  margin-bottom: 1.5rem;
  display: inline-block;
}
.backButton:hover {
  background-color: #d85c00; /* Tom mais escuro de secondary */
}
.backButton:focus-visible {
  outline: 2px solid var(--color-text-light);
  outline-offset: 2px;
  box-shadow: 0 0 0 3px rgba(33, 37, 41, 0.3);
}

.detailHeader {
  text-align: center;
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  /* A cor da borda inferior ser√° aplicada via style inline no JSX */
}

.detailImageLarge {
  width: 100%;
  max-width: 400px; /* Limita a largura da imagem principal */
  height: auto;
  max-height: 350px; /* Limita a altura m√°xima */
  object-fit: cover;
  border-radius: var(--border-radius);
  margin: 0 auto 1rem auto; /* Centraliza e adiciona margem inferior */
  display: block;
  box-shadow: 0 4px 8px var(--color-shadow);
  background-color: #f0f0f0; /* Fundo para imagens carregando */
}

.animalNameTitle {
  font-family: var(--font-primary);
  font-size: 2rem; /* Ajustado */
  font-weight: 800;
  margin: 0.5rem 0 0.25rem 0;
  color: var(--color-primary);
  display: flex;
  flex-direction: column; /* Para o nome cient√≠fico ficar abaixo */
  align-items: center;
  justify-content: center;
}

.scientificNameDetail {
  font-size: 1.25rem; /* Ajustado */
  font-weight: 400;
  font-style: italic;
  color: #444;
  margin-top: 0.25rem;
  display: block; /* Garante que ocupe a largura */
  width: 100%;
}

.extinctionBanner {
  padding: 0.7rem 1.1rem; /* Ajustado */
  border-radius: var(--border-radius);
  color: var(--color-text-light); /* O texto ser√° escuro por padr√£o */
  margin-top: 1rem;
  display: inline-block; /* Para que o fundo se ajuste ao conte√∫do */
}
.extinctionBanner h2 {
  margin: 0;
  font-size: 1.1rem; /* Ajustado */
  font-weight: bold;
}

.extinctionScaleVisual {
  display: flex;
  justify-content: center;
  gap: 5px;
  margin-top: 0.75rem;
  flex-wrap: wrap;
}

.scaleLevel {
  padding: 0.3rem 0.6rem;
  font-size: 0.75rem;
  border-radius: 3px;
  color: var(--color-text-light);
  font-weight: bold;
  min-width: 30px;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.scaleLevelCurrent {
  /* Estilos para o n√≠vel atual na escala */
  border: 2px solid var(--color-text-light);
  transform: scale(1.1);
  box-shadow: 0 0 5px var(--color-shadow);
  z-index: 1; /* Para ficar acima dos outros */
}

/* Estilos gerais para se√ß√µes dentro de AnimalDetail */
.detailSection {
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid var(--color-border-light);
}
.detailSection:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.sectionTitle {
  font-family: var(--font-primary);
  font-size: 1.25rem; /* Ajustado */
  color: var(--color-primary);
  margin-bottom: 1rem;
  border-bottom: 2px solid var(--color-secondary);
  padding-bottom: 0.5rem;
  display: inline-block; /* Para a borda inferior se ajustar ao texto */
}
.sectionTitle .icon {
  /* Para os emojis nas se√ß√µes */
  margin-right: 0.5rem;
}

.sectionSubtitle {
  /* Para t√≠tulos como "Habitats Principais:" */
  font-family: var(--font-primary);
  font-size: 1rem; /* Ajustado */
  color: #333;
  margin-top: 1.2rem;
  margin-bottom: 0.5rem;
  font-weight: 600; /* semibold */
}

.list {
  /* Para listas ul */
  list-style: none;
  padding-left: 0;
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem; /* Espa√ßamento entre itens da lista */
}

.listItem {
  background-color: #f9f9f9; /* Um cinza muito claro */
  padding: 0.6rem 0.85rem;
  border-radius: var(--border-radius-small);
  border-left: 4px solid var(--color-secondary);
  line-height: 1.5;
  font-size: 0.875rem; /* text-sm */
}
.listItem strong {
  color: var(--color-primary);
}
.listItem .icon {
  /* Para emojis nos itens da lista */
  margin-right: 0.3rem;
}

.definitionListDt {
  font-weight: bold;
  color: var(--color-primary);
  text-transform: capitalize; /* Para 'reino' virar 'Reino' */
}
.definitionListDd {
  margin-left: 0.25rem; /* Pequeno recuo para a defini√ß√£o */
}

/* Galeria de Fotos */
.galleryContainer {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: flex-start; /* Alinha no topo se as imagens tiverem alturas diferentes */
  flex-wrap: wrap; /* Permite que as imagens quebrem linha */
}
.galleryImage {
  width: 100%;
  max-width: 320px; /* Limita a largura de cada imagem na galeria */
  height: auto;
  max-height: 280px; /* Limita a altura m√°xima */
  object-fit: cover;
  border-radius: var(--border-radius);
  box-shadow: 0 2px 6px var(--color-shadow);
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  background-color: #f0f0f0;
}
.galleryImage:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px var(--color-shadow);
}

/* Se√ß√£o de Fonte */
.sourceSection p {
  margin-bottom: 0.3rem;
  font-size: 0.8rem; /* Ajustado */
  color: #666;
}
.sourceSection a {
  color: var(--color-secondary);
  text-decoration: none;
}
.sourceSection a:hover {
  text-decoration: underline;
}

.textMuted {
  /* Para mensagens de "Informa√ß√£o n√£o dispon√≠vel" */
  font-style: italic;
  color: var(--color-text-muted);
  font-size: 0.875rem;
}

/* Ajustes responsivos */
@media (min-width: 640px) {
  /* sm */
  .detailContainer {
    padding: 1.5rem;
  }
  .animalNameTitle {
    font-size: 2.5rem;
  }
  .scientificNameDetail {
    font-size: 1.5rem;
  }
  .extinctionBanner h2 {
    font-size: 1.3rem;
  }
  .sectionTitle {
    font-size: 1.5rem;
  }
  .listItem {
    font-size: 0.95rem; /* text-baseish */
  }
  .sourceSection p,
  .sourceSection a {
    font-size: 0.875rem;
  }
}

@media (min-width: 768px) {
  /* md */
  .detailContainer {
    padding: 2rem;
  }
}

/* Estilos para indicar que a imagem da galeria √© clic√°vel */
.galleryImageClickable {
  cursor: zoom-in;
  position: relative; /* Necess√°rio para o overlay do √≠cone, se adicionar */
}

.galleryImageClickable:hover::after {
  /* Exemplo de √≠cone de zoom no hover */
  content: "üîç"; /* Ou um SVG/√≠cone de fonte */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5);
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.5rem;
  border-radius: 50%;
  font-size: 1.2rem;
  pointer-events: none; /* Para que o clique ainda v√° para a imagem */
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.galleryImageClickable:hover::after {
  opacity: 1;
}

.galleryHint {
  font-size: 0.875rem;
  color: var(--color-text-muted);
  text-align: center;
  margin-bottom: 1rem;
}

/* Estilos para o Lightbox/Modal */
.lightboxBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Fundo escuro semi-transparente */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Garante que fique acima de tudo */
  padding: 1rem; /* Espa√ßamento para telas pequenas */
  box-sizing: border-box;
}

.lightboxContent {
  position: relative;
  background-color: #fff; /* Fundo do conte√∫do do modal */
  padding: 10px; /* Pequeno padding ao redor da imagem */
  border-radius: var(--border-radius);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  max-width: 90vw; /* Largura m√°xima de 90% da viewport */
  max-height: 90vh; /* Altura m√°xima de 90% da viewport */
  display: flex; /* Para centralizar a imagem se ela for menor que o max-width/height */
  justify-content: center;
  align-items: center;
}

.lightboxImage {
  max-width: 100%; /* Garante que a imagem n√£o ultrapasse o content */
  max-height: 100%; /* Garante que a imagem n√£o ultrapasse o content */
  display: block; /* Remove espa√ßo extra abaixo da imagem */
  object-fit: contain; /* Mant√©m a propor√ß√£o da imagem */
}

.lightboxCloseButton {
  position: absolute;
  top: -15px; /* Ajuste para posicionar fora da caixa de imagem se desejar */
  right: -15px;
  background-color: white;
  color: var(--color-primary);
  border: 2px solid var(--color-primary);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  font-size: 1.5rem;
  line-height: 26px; /* Ajusta o 'X' verticalmente */
  text-align: center;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: background-color 0.2s, color 0.2s;
}
.lightboxCloseButton:hover {
  background-color: var(--color-primary);
  color: white;
}
.lightboxCloseButton:focus-visible {
  outline: 2px solid var(--color-secondary);
  outline-offset: 2px;
}

/* Ajuste para o bot√£o de fechar em telas menores, para ficar dentro da √°rea vis√≠vel */
@media (max-width: 640px) {
  .lightboxCloseButton {
    top: 5px;
    right: 5px;
    background-color: rgba(
      255,
      255,
      255,
      0.8
    ); /* Levemente transparente para n√£o cobrir muito a imagem */
  }
  .lightboxContent {
    padding: 5px;
  }
}

.galleryImageClickable {
  cursor: zoom-in;
  position: relative;
}
.galleryImageClickable:hover::after {
  content: "üîç";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5);
  color: white;
  background-color: rgba(0, 0, 0, 0.6); /* Um pouco mais escuro */
  padding: 0.6rem; /* Aumentar padding */
  border-radius: 50%;
  font-size: 1.4rem; /* Aumentar tamanho do √≠cone */
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.galleryImageClickable:hover::after {
  opacity: 1;
}

.galleryHint {
  font-size: 0.875rem;
  color: var(--color-text-muted);
  text-align: center;
  margin-bottom: 1rem;
}

/* --- LIGHTBOX ESTILOS REVISADOS --- */
.lightboxBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85); /* Mais escuro para maior foco */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 10px; /* Pequeno padding para n√£o colar nas bordas da tela */
  box-sizing: border-box;
}

.lightboxContentWrapper {
  /* Este √© o container principal do modal */
  position: relative;
  display: flex; /* Para alinhar bot√µes de navega√ß√£o e a imagem */
  align-items: center;
  justify-content: center;
  width: 100%; /* Ocupa a largura do backdrop (com padding) */
  height: 100%; /* Ocupa a altura do backdrop (com padding) */
  /* Removido max-width e max-height daqui para permitir que a imagem dite o tamanho at√© os limites do backdrop */
}

.lightboxImageContainer {
  /* Novo container para a imagem, dentro do wrapper */
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%; /* A imagem n√£o exceder√° o wrapper */
  max-height: 100%;
  overflow: hidden; /* Caso a imagem precise ser contida */
  /* Adicionar uma sombra sutil na imagem em si, se desejar */
  /* box-shadow: 0 0 25px rgba(0,0,0,0.5); */
}

.lightboxImage {
  display: block;
  max-width: 100%; /* Imagem usa 100% do seu container (.lightboxImageContainer) */
  max-height: 100%; /* que por sua vez √© limitado pelo .lightboxContentWrapper/backdrop */
  object-fit: contain; /* Mant√©m a propor√ß√£o e garante visibilidade total */
  border-radius: 3px; /* Pequena borda arredondada na imagem, opcional */
  user-select: none; /* Evita sele√ß√£o de imagem acidental */
  -webkit-user-drag: none; /* Evita arrastar imagem no Safari */
}

.lightboxNavButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(30, 30, 30, 0.6); /* Cor sutil */
  color: white;
  border: none;
  border-radius: 50%;
  width: 44px; /* Um pouco maior */
  height: 44px;
  font-size: 2rem; /* √çcone maior */
  line-height: 44px;
  text-align: center;
  cursor: pointer;
  z-index: 1010;
  transition: background-color 0.2s, opacity 0.2s;
  opacity: 0.6; /* Menos opaco por padr√£o */
  outline: none; /* Remove outline padr√£o */
}
.lightboxNavButton:hover,
.lightboxNavButton:focus-visible {
  /* Destaque no hover e focus */
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 1;
  outline: 2px solid var(--color-secondary); /* Adiciona um anel de foco customizado */
  outline-offset: 2px;
}

.lightboxPrevButton {
  left: 15px; /* Mais espa√ßo da borda */
}

.lightboxNextButton {
  right: 15px; /* Mais espa√ßo da borda */
}

.lightboxCloseButton {
  position: absolute;
  top: 15px; /* Mais pr√≥ximo da borda superior */
  right: 15px;
  background-color: rgba(30, 30, 30, 0.6);
  color: white;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  font-size: 1.8rem;
  line-height: 36px;
  text-align: center;
  cursor: pointer;
  font-weight: normal; /* 'X' menos "bold" */
  z-index: 1020;
  transition: background-color 0.2s, transform 0.2s;
  opacity: 0.7;
  outline: none;
}
.lightboxCloseButton:hover,
.lightboxCloseButton:focus-visible {
  background-color: rgba(0, 0, 0, 0.9);
  transform: scale(1.1); /* Efeito sutil de zoom */
  opacity: 1;
  outline: 2px solid var(--color-secondary);
  outline-offset: 2px;
}

/* Media queries para telas muito pequenas, se os bot√µes ficarem apertados */
@media (max-width: 480px) {
  .lightboxNavButton {
    width: 38px;
    height: 38px;
    font-size: 1.5rem;
    line-height: 38px;
  }
  .lightboxPrevButton {
    left: 5px;
  }
  .lightboxNextButton {
    right: 5px;
  }
  .lightboxCloseButton {
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    font-size: 1.5rem;
    line-height: 30px;
  }
  .lightboxBackdrop {
    padding: 5px;
  } /* Reduz padding do backdrop */
}

