/* --- ESTILOS REVISADOS PARA O TAZO FLIPPABLE NO HEADER --- */
.headerTazoContainer {
  width: 100%;
  /* TAMANHO AJUSTADO: Defina um tamanho mais apropriado para o header */
  /* Por exemplo, um tamanho similar ao do AnimalCard ou um pouco menor/maior */
  max-width: 250px; /* Exemplo: Reduzido de 320px */
  height: 250px; /* Exemplo: Reduzido de 280px e tornado quadrado, ajuste conforme a forma do seu tazo */
  margin: 0 auto 1.5rem auto;
  perspective: 1000px;
  cursor: pointer;
  display: block;
  border-radius: var(--border-radius);
  outline: none;
}
.headerTazoContainer:focus-visible {
  /* Mant√©m feedback de foco para acessibilidade do flip */
  box-shadow: 0 0 0 2px var(--color-bg-light), 0 0 0 4px var(--color-secondary);
}

.headerTazoInner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.7s cubic-bezier(0.4, 0, 0.2, 1);
  transform-style: preserve-3d;
  border-radius: var(--border-radius);
}

.headerTazoInnerFlipped {
  transform: rotateY(180deg);
}

.headerTazoFace {
  position: absolute; /* CRUCIAL para o flip: ambas as faces ocupam o mesmo espa√ßo */
  width: 100%;
  height: 100%;
  backface-visibility: hidden; /* CRUCIAL: esconde a face que est√° "atr√°s" */
  -webkit-backface-visibility: hidden; /* Prefixo para Safari */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: var(--border-radius);
  overflow: hidden;
  background-color: #ffffff;
}
.headerTazoFace img {
  width: 100%;
  height: 100%;
  object-fit: contain; /* ou 'cover' dependendo da imagem do tazo */
}

.headerTazoFront {
  /* z-index: 1; Opcional, se houver problemas de sobreposi√ß√£o estranhos, mas geralmente n√£o √© necess√°rio com backface-visibility */
}

.headerTazoBack {
  transform: rotateY(180deg); /* CRUCIAL: posiciona a face de tr√°s */
  /* z-index: 0; Opcional */
}

.imagePlaceholder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: var(--color-text-muted);
  font-size: 0.9rem;
  background-color: #e9ecef;
  border-radius: var(--border-radius);
}

