  border-radius: 50%;
  font-size: 1.4rem;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.galleryImageClickable:hover::after {
  opacity: 1;
}

/* Classe para a imagem est√°tica grande se o tazo n√£o puder ser mostrado (fallback) */
.detailImageLarge {
  width: 100%;
  max-width: 300px; /* Ajuste conforme o design */
  height: auto; /* Ou uma altura fixa se preferir, ex: 300px */
  max-height: 300px;
  object-fit: cover; /* Ou contain */
  border-radius: var(--border-radius);
  margin: 0 auto 1.5rem auto;
  display: block;
  box-shadow: 0 4px 8px var(--color-shadow);
  background-color: #f0f0f0;
}
