/* src/index.css */
:root {
  --font-primary: "Nunito", sans-serif;
  --font-secondary: "Open Sans", sans-serif;

  --color-bg-light: #f0f2f5;
  --color-text-light: #212529;
  --color-text-muted: #6c757d;
  --color-primary: #2e7d32;
  --color-secondary: #ef6c00;
  --color-card-bg: #ffffff;
  --color-shadow: rgba(0, 0, 0, 0.1);
  --color-border-light: #dee2e6;

  /* Cores de N√≠vel de Extin√ß√£o */
  --color-lc: #aed581;
  --color-nt: #dce775;
  --color-vu: #ffee58;
  --color-en: #ffa726;
  --color-cr: #ef5350;
  --color-ew: #7e57c2;
  --color-ex: #616161;
  --color-dd: #bdbdbd;
  --color-ne: #e0e0e0;
  --color-default-extinction: #cccccc;

  --border-radius: 8px;
  --border-radius-small: 4px;
}

body {
  font-family: var(--font-secondary);
  background-color: var(--color-bg-light);
  color: var(--color-text-light);
  margin: 0;
  line-height: 1.6;
}

html,
body,
#root {
  min-height: 100%; /* Garante que o app possa ocupar a altura toda */
}

#root {
  display: flex;
  flex-direction: column;
}

/* Estilos para scrollbar (opcional) */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Classe global para esconder visualmente mas manter acess√≠vel */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Mensagens de erro ou carregamento globais */
.loading-message,
.error-message {
  text-align: center;
  font-size: 1.2rem;
  padding: 2rem;
  color: var(--color-text-muted);
}
