/* src/components/AnimalCard.module.css */
.animalCard {
  background-color: var(--color-card-bg);
  border-radius: var(--border-radius);
  box-shadow: 0 4px 8px var(--color-shadow);
  cursor: pointer;
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
  min-height: 340px;
  perspective: 1000px; /* Para o efeito de flip */
  outline: none; /* Reset de outline */
}

.animalCard:hover,
.animalCard:focus-visible {
  /* :focus-visible para melhor acessibilidade com teclado */
  transform: translateY(-6px) scale(1.02); /* Aumentei um pouco o efeito */
  box-shadow: 0 8px 16px var(--color-shadow);
}
.animalCard:focus-visible {
  /* Adiciona um anel de foco vis√≠vel se quiser, al√©m do transform */
  box-shadow: 0 0 0 2px var(--color-bg-light), 0 0 0 4px var(--color-secondary),
    0 8px 16px var(--color-shadow);
}

.cardInner {
  position: relative;
  width: 100%;
  height: 208px; /* 52 * 4 (h-52 do Tailwind) */
  margin-bottom: 0.75rem; /* mb-3 */
  transition: transform 0.7s;
  transform-style: preserve-3d;
}

.cardInnerFlipped {
  transform: rotateY(180deg);
}

.cardFace {
  /* Comum para front e back */
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: var(
    --border-radius-small
  ); /* Ajuste para coincidir com o card */
  border-top-right-radius: var(--border-radius-small);
  overflow: hidden;
  background-color: #f3f4f6; /* bg-gray-100 */
}
.cardFace img {
  width: 100%;
  height: 100%;
  object-fit: contain; /* Para n√£o cortar a imagem do tazo */
}

.cardBack {
  transform: rotateY(180deg);
}

.cardContent {
  padding: 0 1rem 1rem 1rem; /* p-4 pt-0 */
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.animalName {
  font-family: var(--font-primary);
  font-size: 1.25rem; /* text-xl */
  font-weight: 700; /* font-bold */
  margin-bottom: 0.125rem; /* mb-0.5 */
  color: var(--color-primary);
}

.scientificName {
  font-size: 0.75rem; /* text-xs */
  font-style: italic;
  color: #4b5563; /* text-gray-600 */
  margin-bottom: 0.5rem; /* mb-2 */
  word-break: break-all;
}

.extinctionStatus {
  padding: 0.25rem 0.625rem; /* py-1 px-2.5 */
  border-radius: var(--border-radius-small);
  font-weight: 700; /* font-bold */
  font-size: 0.75rem; /* text-xs */
  text-align: center;
  align-self: flex-start;
  color: var(--color-text-light); /* Cor de texto padr√£o */
}

/* Media queries para responsividade do card */
@media (min-width: 640px) {
  /* sm breakpoint */
  .animalName {
    font-size: 1.5rem; /* text-2xl */
  }
  .scientificName {
    font-size: 0.875rem; /* text-sm */
  }
}
