/* src/components/Header.module.css */
.header {
  background-color: var(--color-primary);
  color: white;
  padding: 1rem 1rem; /* Ajustado para mobile first */
  box-shadow: 0 2px 4px var(--color-shadow);
  position: sticky;
  top: 0;
  z-index: 50; /* Tailwind z-50 */
}

.headerContentWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem; /* 12px */
  max-width: 1152px; /* max-w-6xl */
  margin-left: auto;
  margin-right: auto;
}

.logoAndTitle {
  display: flex;
  align-items: center;
  gap: 0.75rem; /* 12px */
}

.logo {
  height: 40px; /* Ajuste conforme necess√°rio */
}

.title {
  font-family: var(--font-primary);
  font-weight: 800; /* Tailwind font-extrabold */
  font-size: 1.875rem; /* Tailwind text-3xl */
  line-height: 2.25rem;
}

.randomButton {
  background-color: var(--color-secondary);
  color: white;
  font-family: var(--font-primary);
  padding: 0.5rem 1rem;
  border-radius: var(--border-radius-small);
  font-size: 0.875rem; /* Tailwind text-sm */
  white-space: nowrap;
  transition: background-color 0.2s;
}
.randomButton:hover {
  background-color: #d85c00; /* Tom mais escuro de secondary */
}

.searchContainer {
  margin-top: 1rem;
  width: 100%;
  max-width: 768px; /* max-w-3xl */
  margin-left: auto;
  margin-right: auto;
}

.searchInput {
  width: 100%;
  padding: 0.75rem; /* p-3 */
  border-radius: var(--border-radius);
  border: 1px solid #ccc;
  color: var(--color-text-light);
  outline: none;
}
.searchInput:focus {
  border-color: var(--color-secondary);
  box-shadow: 0 0 0 2px rgba(239, 108, 0, 0.3); /* Similar ao ring */
}

.sortControls {
  margin-top: 0.75rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.5rem; /* gap-2 */
}

.sortButton {
  background-color: var(--color-secondary);
  color: white;
  padding: 0.375rem 0.75rem; /* py-1.5 px-3 */
  border-radius: var(--border-radius-small);
  font-size: 0.75rem; /* text-xs */
  border: none;
  cursor: pointer;
  transition: background-color 0.2s;
}
.sortButton:hover {
  background-color: #d85c00;
}

/* Media queries para responsividade, se necess√°rio */
@media (min-width: 640px) {
  /* sm breakpoint */
  .header {
    padding: 1rem 2rem;
  }
  .headerContentWrapper {
    flex-direction: row;
    justify-content: space-between;
  }
  .logo {
    height: 48px;
  }
  .title {
    font-size: 2.25rem; /* text-4xl */
  }
  .randomButton {
    order: 0; /* Reseta a ordem */
  }
  .sortButton {
    font-size: 0.875rem; /* text-sm */
  }
}
